
import './App.css';
import lajoyalogo from './Assets/lajoya.png';
import tictactoelogo from './Assets/icon.png';
import background from './Assets/bg2.png'
import { useEffect, useState } from 'react';
import TicTacToe from './TicTacToe';

export enum Screens {
  WELCOME = 'WELCOME',
  HOME = 'HOME',
  TICTACTOE = 'TICTACTOE',
}

function App() {

  const [currentScreen, setCurrentScreen] = useState<Screens>(Screens.WELCOME);

  useEffect(() => {
    const timer = setTimeout(() => {
      setCurrentScreen(Screens.HOME);
    },5000);

    return () => {
      clearTimeout(timer);
    }
  },[])

  const getCurrentScreenContent = (screen: Screens) => {
    switch (screen) {
      case Screens.WELCOME:
        return (
          <div className="fx-col" style={{alignItems: 'center', justifyContent:'center'}}>
            <div className='appName'>Welcome to</div>
            <img src={lajoyalogo} style={
              {
                width: '128px',
                height: '128px',
              }
            } />
            <div className='appName'>LaJoya Games</div>
          </div>
          
        );
      case Screens.HOME:
        return (
          <div className="fx-col" onClick={() => setCurrentScreen(Screens.TICTACTOE)}>
            <img src={tictactoelogo} style={
              {
                width: '128px',
                height: '128px',
              }
            } />
            <div className='gameName'>Infinite TicTacToe</div>
          </div>
        );
      case Screens.TICTACTOE:
        return (<TicTacToe goHome={()=> setCurrentScreen(Screens.HOME)}/>);
      default:
        return (<div></div>);

    }
  }

  return (
    <div className="App" style={{
      width: '100%',
      height: '100%',
      backgroundImage: `url(${background})`,
      backgroundRepeat: "no-repeat",
      backgroundSize: 'cover',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    }}>

      {getCurrentScreenContent(currentScreen)}

    </div>
  );
}

export default App;
