import { io } from 'socket.io-client';


export const socket = io("https://smdp-test.webpubsub.azure.com", {
    path: "/clients/socketio/hubs/Hub",
});

export const sendMessage = (arg: string) => {
    socket.emit("hello", arg);
}

export const startGame = (rommname: string) => {
    socket.emit("startGame", rommname);
}

export const sendMove = (roomname: string, index: number) => {
    socket.emit("sendMove", roomname, index);
}

export const createRoom = (roomname: string) => {
    socket.emit("createRoom", roomname);
}

export const joinRoom = (roomname: string, username: string) => {
    socket.emit("joinRoom", roomname, username);
}

export const updateMembers = (roomname: string, usernames: string[]) => {
    socket.emit("updateMembers", roomname, usernames);
}

