import { DefaultButton, Label, TextField } from '@fluentui/react';
import React, { useEffect, useState } from 'react';
import { createRoom, joinRoom, socket, startGame, updateMembers } from '../client';

interface OnlineLobbyProps {
    onProceed: any;
};

function OnlineLobby(props: OnlineLobbyProps) {

    const getErrorMessage = (value: string): string => {
        return value.length > 0 && value.length < 8 ? '' : `Name should have 1-8 characterrs`;
    };

    const [player1, setPlayer1] = useState('');
    const [player2, setPlayer2] = useState('');
    const [roomName, setRoomname] = useState('');

    const [showLobby, setShowLobby] = useState(false);
    const [isCreator, setIsCreator] = useState(false);
    const [player2Joined, setPlayer2Joined] = useState(false);

    const onProceedClicked = () => {
        console.log(player1, player2);
        if (player1.length === 0 || player1.length >= 8 || player1.length === 0 || player2.length >= 8) return;
        else {
            const players = [player1, player2];
            console.log(players);
            props.onProceed(players, roomName, isCreator);
        }
    }

    const onStartGame = () =>{
        startGame(roomName);
        onProceedClicked();
    }

    useEffect(() => {
        const onUserJoin = (username: string) => {
            if (isCreator) {
                setPlayer2(username);
                setPlayer2Joined(true);
                const players = [player1, username];
                updateMembers(roomName, players);
            }
        };
        const onUpdateMembers = (players: string[]) => {
            console.log('user update', players);
            setPlayer1(players[0] || '');
            setPlayer2(players[1] || '');
            setPlayer2Joined(true);
        };

        socket.on("userJoin", onUserJoin);
        socket.on("updateMembers", onUpdateMembers);
        socket.on("startGame", onProceedClicked);

        return () => {
            socket.off("userJoin", onUserJoin);
            socket.off("updateMembers", onUpdateMembers);
            socket.off("startGame", onProceedClicked);
        }
    }, [isCreator, player1])



    const onJoinRoom = () => {
        if (player1.length === 0 || player1.length >= 8 || roomName.length === 0 || roomName.length >= 8) return;
        else {
            joinRoom(roomName, player1);
            setShowLobby(true);
            console.log('joining', isCreator, showLobby);
        }
    }

    const onCreateRoom = () => {
        if (player1.length === 0 || player1.length >= 8 || roomName.length === 0 || roomName.length >= 8) return;
        else {
            createRoom(roomName);
            setShowLobby(true);
            setIsCreator(true);
            console.log('creating', isCreator, showLobby);
        }
    }

    const textFieldStyles = {
        root: {
            fontSize: '25px',
            width: '80%'
        },
        fieldGroup: {
            borderRadius: 10,
            border: "0px solid transparent",
            background: "#F3F2F1",
            height: '45px',
        },
        field: {
            color: "#e87373",
            fontSize: '25px',
        }
    }

    const labelStyles = {
        root: {
            fontSize: '18px',
            fontWeight: 'bold',
            textAlign: 'left',
            width: '80%',
        }
    }

    const lobbyStyles = {
        root: {
            fontSize: '18px',
            fontWeight: 'bold',
            textAlign: 'center',
            width: '80%',
        }
    }

    const buttonStyles = {
        root: {
            borderRadius: '10px',
            backgroundColor: '#e87373',
            width: '40%',
            marginTop: '10px',

            height: '45px'
        },
        textContainer: {
            padding: '15px',
            fontWeight: 'bold',
        },
        label: {
            fontWeight: 'bold',
            fontSize: '20px',
        }
    }

    return (
        showLobby ?
            (<div className='fx-col fwt' style={{
                gap: '10px',
                justifyContent: 'center',
                alignItems: 'center',
            }}>
                
                {isCreator && <Label styles={{
                    root: {
                        fontSize: '25px',
                        fontWeight: 'bold',
                        textAlign: 'center',
                        width: '80%',
                        color: '#e87373',
                    }
                }}>Room Name: {roomName}</Label>}
                <Label styles={{
                    root: {
                        fontSize: '20px',
                        fontWeight: 'bold',
                        textAlign: 'center',
                        width: '80%',
                        color: '#e87373',
                    }
                }}>Players Joined :</Label>
                <Label styles={lobbyStyles}>{player1}</Label>
                
                {player2Joined ?
                    (<Label styles={lobbyStyles}>{player2}</Label>) : (<Label styles={lobbyStyles}>Waiting for Player 2....</Label>)}
                {isCreator
                    ? <DefaultButton styles={buttonStyles} text="Start" onClick={onStartGame} disabled={!player2Joined} />
                    : (<Label styles={lobbyStyles}>Waiting for host to start game....</Label>)}

            </div>) :
            (<div className='fx-col fwt' style={{
                gap: '10px',
                justifyContent: 'center',
                alignItems: 'center',
            }}>
                <Label required styles={labelStyles}>Player Name</Label>
                <TextField value={player1} onChange={(e) => setPlayer1((e.target as HTMLInputElement).value)} styles={textFieldStyles} validateOnFocusOut onGetErrorMessage={getErrorMessage} />
                <Label required styles={labelStyles}>Room Name</Label>
                <TextField value={roomName} onChange={(e) => setRoomname((e.target as HTMLInputElement).value)} styles={textFieldStyles} validateOnFocusOut onGetErrorMessage={getErrorMessage} />
                <div className="fx-row" style={{ gap: '20px', justifyContent: 'center' }}>
                    <DefaultButton styles={buttonStyles} text="Join" onClick={onJoinRoom} allowDisabledFocus />
                    <DefaultButton styles={buttonStyles} text="Create" onClick={onCreateRoom} allowDisabledFocus />

                </div>

            </div>)
    );
}

export default OnlineLobby;