import React, { useState } from 'react';
import Board from './Board';
import zero from '../Assets/zero.png';
import cross from '../Assets/crosss.png';

interface GameProps {
    players: string[];
    onWin: any;
    isOnline: boolean;
    isPlayer1: boolean;
    room: string;
};

function TTTGame(props: GameProps) {
    const { players, onWin, isOnline, isPlayer1, room } = props;
    const [currentPlayer, setCurrentPlayer] = useState(0);

    const icons = [zero, cross];
    return (
        <div className='fx-col fht fwt' style={{
            gap: '20px'
        }}>
            <div className="fx-row fwt" style={{ height: '60%', marginTop: '70px' }}>
                <Board room={room} isOnline={isOnline} isPlayer1={isPlayer1} currentPlayer={currentPlayer} setCurrentPlayer={setCurrentPlayer} onWin={onWin} />
            </div>
            <div className="fx-row" style={{ justifyContent: 'space-around', alignItems: 'center', gap: '20px' }}>
                {players.map((player, index) => {
                    return <div key={`${index}_${currentPlayer}`} style={{
                        height: '85px',
                        width: '30%',
                        fontSize: '20px',
                        fontWeight: 'bold',
                        border: '2px solid black',
                        borderRadius: 10,
                        textAlign: 'center',
                        alignContent: 'center',
                        backgroundColor: index === currentPlayer ? '#79c479' : 'none',
                    }}>
                        <div>{player}</div>
                        <img src={icons[index]} style={
                            {
                                width: '35px',
                                height: '35px',
                                marginTop:'10px',
                            }
                        } />
                    </div>
                })}
            </div>
            
        </div>
    );
}

export default TTTGame;