import React, { useState } from 'react';
import home from '../Assets/homee.png';
import win from '../Assets/winner.png';
import Players from './Players';
import TTTGame from './Game';
import { DefaultButton, Pivot, PivotItem } from '@fluentui/react';
import OnlineLobby from './OnlineLobby';

export interface GameProps {
    goHome: any;
}

export enum GameScreens {
    PLAYERS = 'PLAYERS',
    GAME = 'GAME',
    RESULT = 'RESULT'
}

function TicTacToe(props: GameProps) {

    const { goHome } = props;
    const [gameScreen, setgameScreen] = useState(GameScreens.PLAYERS);
    const [players, setPlayers] = useState<string[]>([]);
    const [winner, setWinner] = useState<string>('');
    const [room, setRoom] = useState<string>('');
    const [onlineMode, setOnlineMode] = useState(false);
    const [isPlayer1, setIsPlayer1] = useState(false);

    const playersToGameScreenMoveOnline = (playerlist: string[],roomname:string, isPlayer1?:boolean) => {
        setPlayers(playerlist);
        setIsPlayer1(!!isPlayer1);
        setOnlineMode(true);
        setRoom(roomname);
        setgameScreen(GameScreens.GAME);
    }

    const playersToGameScreenMove = (playerlist: string[]) => {
        setPlayers(playerlist);
        setgameScreen(GameScreens.GAME);
    }

    const onWin = (index: number) => {
        setWinner(players[index]);
        setgameScreen(GameScreens.RESULT);
    }

    const getCurrentScreenContent = (screen: GameScreens) => {
        switch (screen) {
            case GameScreens.PLAYERS:
                return (
                    <div className="fx-col fwt fht" style={{
                        justifyContent: 'center',
                    }}>
                        <Pivot aria-label="Basic Pivot Example" styles={{
                            itemContainer:{
                                marginTop: '50px',
                            },
                            linkContent:{
                                fontSize: '20px',
                                fontWeight: 'bold',
                            },
                            linkIsSelected:{
                                backgroundColor: 'transparent',
                            },
                            link:{
                                marginLeft: '20%',
                            }
                            
                        }}>
                            <PivotItem
                                headerText="Online"
                            >
                                <OnlineLobby onProceed={playersToGameScreenMoveOnline} />
                            </PivotItem>
                            <PivotItem headerText="Local">
                                <Players onProceed={playersToGameScreenMove} />

                            </PivotItem>

                        </Pivot>
                    </div>


                );
                break;
            case GameScreens.GAME:
                return (<TTTGame room={room} isOnline={onlineMode} isPlayer1={isPlayer1} players={players} onWin={onWin}></TTTGame>);
                break;
            default:
                return (<div className="fx-col fwt" style={{ height: '60%', marginTop: '70px', justifyContent: 'center', alignItems: 'center' }}>
                    <img src={win} style={
                        {
                            width: '128px',
                            height: '128px',
                            marginBottom: '20px'
                        }
                    } />
                    <div style={{
                        textAlign: 'center',
                        color: '#f74354',
                        height: '40px',
                        fontSize: '30px',
                        fontWeight: 'bold',
                    }}>{winner} wins!!</div>
                    <DefaultButton styles={{
                        root: {
                            borderRadius: '10px',
                            backgroundColor: '#e87373',
                            width: '60%',
                            marginTop: '30px',

                            height: '45px'
                        },
                        textContainer: {
                            padding: '15px',
                            fontWeight: 'bold',
                        },
                        label: {
                            fontWeight: 'bold',
                            fontSize: '20px',
                        }
                    }} text="Return to Home" onClick={goHome} allowDisabledFocus />
                </div>);

        }
    };

    return (
        <div className='fx-row fht fwt'>
            <div className="fx-col" onClick={goHome} style={{
                position: 'absolute',
                top: 20,
                right: 20,
            }}>
                <img src={home} style={
                    {
                        width: '48px',
                        height: '48px',
                    }
                } />
            </div>
            <div className="fx-row fht fwt">
                {getCurrentScreenContent(gameScreen)}
            </div>
        </div>
    );
}

export default TicTacToe;