import { DefaultButton, Label, TextField } from '@fluentui/react';
import React, { useState } from 'react';

interface PlayerProps  {
    onProceed: any;
};

function Players(props: PlayerProps) {

    const getErrorMessage = (value: string): string => {
        return value.length > 0 && value.length < 8  ? '' : `Name should have 1-8 characterrs`;
      };

      const [player1, setPlayer1] = useState('');
      const [player2, setPlayer2] = useState('');

      const onProceedClicked = () => {
        if(player1.length === 0 || player1.length >= 8 || player2.length === 0 || player2.length >= 8) return;
        else{
            const players =[player1,player2];
            console.log(players);
            props.onProceed(players);
        }
      }

      const textFieldStyles = {
        root:{
            fontSize: '25px',
            width: '80%'
        },
        fieldGroup: {
            borderRadius: 10,
            border: "0px solid transparent",
            background: "#F3F2F1",
            height: '45px',
          },
          field: {
            color: "#e87373",
            fontSize: '25px',
          }
    }

    const labelStyles = {
        root:{
            fontSize: '18px',
            fontWeight: 'bold',
            textAlign: 'left',
            width: '80%',
        }
    }

    const buttonStyles = {
        root:{
            borderRadius: '10px',
            backgroundColor: '#e87373',
            width: '60%',
            marginTop: '10px',
            
            height: '45px'
        },
        textContainer: {
            padding: '15px',
            fontWeight: 'bold',
        },
        label:{
            fontWeight: 'bold',
            fontSize: '20px',
        }
        }


    return (
        <div className='fx-col fwt' style={{
            gap: '10px',
            justifyContent: 'center',
            alignItems: 'center',
        }}>
            <Label required styles={labelStyles}>Player 1</Label>
            <TextField value={player1} onChange={(e)=> setPlayer1((e.target as HTMLInputElement).value)} styles={textFieldStyles} validateOnFocusOut onGetErrorMessage={getErrorMessage} />
            <Label required styles={labelStyles}>Player 2</Label>
            <TextField value={player2} onChange={(e)=> setPlayer2((e.target as HTMLInputElement).value)} styles={textFieldStyles} validateOnFocusOut onGetErrorMessage={getErrorMessage} />
            <DefaultButton styles={buttonStyles} text="Proceed" onClick={onProceedClicked} allowDisabledFocus />
            
        </div>
    );
}

export default Players;