// Importing the useState hook, useEffect hook and useRef hook
import React, { useEffect, useState } from 'react';
import zero from '../Assets/zero.png';
import cross from '../Assets/crosss.png';
import { sendMove, socket } from '../client';

interface BoardProps {
    currentPlayer: number;
    setCurrentPlayer: any;
    onWin: any;
    isOnline: boolean;
    isPlayer1: boolean;
    room: string;
};

const Board = (props: BoardProps) => {
    // Creating a turn state, which indicates the current turn
    const { currentPlayer, setCurrentPlayer, onWin, isOnline, isPlayer1, room } = props;

    const grid = [];
    const [zeroIndex, setZeros] = useState<number[]>([]);
    const [crossIndex, setCrosses] = useState<number[]>([]);
    const [winnerIndex, setWinnerIndex] = useState<number[]>([]);
    const [wrongMove, setWrongMove] = useState(false);

    const isCurrentPlayer = () => {
        return ((!!isPlayer1 && currentPlayer === 0) || (!isPlayer1 && currentPlayer === 1));
    }

    let timer: string | number | NodeJS.Timeout | undefined;
    let winnerTimer: string | number | NodeJS.Timeout | undefined;

    for (let row = 0; row < 3; row++) {
        const currentRow = [];
        for (let col = 0; col < 3; col++) {
            currentRow.push("");
        }
        grid.push(currentRow);
    }

    const setWrongMovePlayed = () => {
        setWrongMove(true);
        clearTimeout(timer);
        timer = setTimeout(() => {
            setWrongMove(false)
        }, 3000);
    }

    const onMove = (index: number) => {
        if (!!isOnline) {
            if (isCurrentPlayer()) {
                console.log('sendMove')
                sendMove(room, index);
                onMoveDone(index)
            }
        }
        else {
            onMoveDone(index);
        }
    }

    const onMoveDone = (index: number) => {
        let arrayToPlay;
        const checkWinner = currentPlayer;

        if (currentPlayer === 0) {
            if (crossIndex.includes(index) || zeroIndex.includes(index)) {
                setWrongMovePlayed();
                return;
            }
            arrayToPlay = [...zeroIndex];
            if (arrayToPlay.length === 3) {
                arrayToPlay = arrayToPlay.slice(1);
            }
            arrayToPlay.push(index);
            setZeros(arrayToPlay);
            setCurrentPlayer(1)
        }
        else {
            if (zeroIndex.includes(index) || crossIndex.includes(index)) {
                setWrongMovePlayed();
                return;
            }
            arrayToPlay = [...crossIndex];
            if (arrayToPlay.length === 3) {
                arrayToPlay = arrayToPlay.slice(1);
            }
            arrayToPlay.push(index);
            setCrosses(arrayToPlay);
            setCurrentPlayer(0)
        }
        if (arrayToPlay.length === 3) {
            if (checkWin(arrayToPlay)) {
                setWinnerIndex(arrayToPlay);
                clearTimeout(winnerTimer);
                winnerTimer = setTimeout(() => {
                    onWin(checkWinner);
                }, 5000);

            }
        }
    }

    useEffect(() => {

        socket.on('receiveMove', onMoveDone);

        return () => {
            socket.off('receiveMove', onMoveDone);
        };
    }, [currentPlayer]);

    const getGridItem = (index: number) => {
        if (zeroIndex.includes(index)) {
            return (
                <img src={zero} style={
                    {
                        width: '50px',
                        height: '50px',
                        marginTop: '10px',
                        opacity: zeroIndex.length === 3 && index === zeroIndex[0] ? '0.3' : index === zeroIndex[1] ? '0.6' :'1',
                    }
                } />
            )
        }
        else if (crossIndex.includes(index)) {
            return (
                <img src={cross} style={
                    {
                        width: '50px',
                        height: '50px',
                        marginTop: '10px',
                        opacity: crossIndex.length === 3 && index === crossIndex[0] ? '0.3' : index === crossIndex[1] ? '0.6' :'1',
                    }
                } />
            )
        }
        else return <div></div>;
    }

    const checkRow = (indexes: number[]) => {
        let sortedIndex = [...indexes].sort();
        if (sortedIndex[0] === 0 || sortedIndex[0] === 3 || sortedIndex[0] === 6) {
            if (sortedIndex[0] === sortedIndex[1] - 1 && sortedIndex[0] === sortedIndex[2] - 2) {
                return true;
            }
        }
        return false;
    };

    // Checks for the win condition in cols
    const checkCol = (indexes: number[]) => {
        let sortedIndex = [...indexes].sort();
        if (sortedIndex[0] === 0 || sortedIndex[0] === 1 || sortedIndex[0] === 2) {
            if (sortedIndex[0] === sortedIndex[1] - 3 && sortedIndex[0] === sortedIndex[2] - 6) {
                return true;
            }
        }
        return false;
    };

    // Checks for the win condition in diagonals
    const checkDiagonal = (indexes: number[]) => {
        let sortedIndex = [...indexes].sort();
        if (sortedIndex[0] === 0 && sortedIndex[1] === 4 && sortedIndex[2] === 8) {
            return true;
        }
        if (sortedIndex[0] === 2 && sortedIndex[1] === 4 && sortedIndex[2] === 6) {
            return true;
        }
        return false;
    };


    const checkWin = (indexes: number[]) => {
        return (
            checkRow(indexes) || checkCol(indexes) || checkDiagonal(indexes)
        );
    };

    return (
        <div className="fx-col fwt" style={{
            justifyContent: 'center',
            alignItems: 'center',
        }}>
            <div className='fwt' style={{
                textAlign: 'center',
                color: '#f74354',
                height: '30px',
                fontSize: '20px',
                fontWeight: 'bold',
                marginBottom: '10px',
            }}>{wrongMove ? 'Wrong Move!' : ''}</div>



            <div className="fx-col fwt" style={{
                justifyContent: 'center',
            }}>
                {grid.map((row, rowId) => {
                    return (
                        <div className='fx-row' style={{
                            justifyContent: 'center',
                        }} key={rowId}>
                            {row.map((node, colId) => {
                                const indexNo = (rowId * 3 + colId);
                                return (
                                    <div key={indexNo} style={{
                                        height: '100px',
                                        width: '30%',
                                        fontSize: '20px',
                                        fontWeight: 'bold',
                                        border: '2px solid black',
                                        borderRadius: 0,
                                        textAlign: 'center',
                                        alignContent: 'center',
                                        backgroundColor: winnerIndex.includes(indexNo) ? '#79c479' : 'transparent'
                                    }} onClick={() => onMove((rowId * 3 + colId))}>
                                        {getGridItem(indexNo)}
                                    </div>
                                );
                            })}
                        </div>
                    )
                })}
            </div>

            {isOnline && (<div className='fwt' style={{
                textAlign: 'center',
                color: '#384b7d',
                height: '30px',
                fontSize: '20px',
                fontWeight: 'bold',
                marginTop: '10px',
                border: isCurrentPlayer() ? '2px solid #384b7d' : '2px solid #f74354' ,
                borderRadius: '15px',
                padding: '2px 12px',
                width: 'fit-content',
            }}>{isCurrentPlayer() ? 'Your Turn' : 'Opponent\'s Turn'}</div>)}

        </div>
    )
};

export default Board;